<template>
  <div class="homeManage">
    <div class="menu">
      <div class="content">
        <div v-for="item in typeList" :key="item.name">{{ item.name }}</div>
      </div>
    </div>
    <div class="operate-btns">
      <el-button @click="addType(typeList)">新增类型</el-button>
      <el-button @click="save(typeList)">保存</el-button>
    </div>
    <div class="type-list">
      <div class="type" v-for="(type, typeIndex) in typeList" :key="typeIndex">
        <div class="type-header">
          <div class="type-title">{{ type.name }}</div>
          <div class="">
            <el-button @click="delType(typeList, typeIndex)">删除</el-button>
            <el-button
              v-if="typeIndex != 0"
              @click="moveUp(typeList, typeIndex)"
            >
              上移
            </el-button>
            <el-button
              v-if="typeIndex != typeList.length - 1"
              @click="moveDown(typeList, typeIndex)"
            >
              下移
            </el-button>
            <el-button @click="add(type.list)">新增商品</el-button>
          </div>
        </div>
        <div class="goods-list" v-if="type.list">
          <div
            class="goods"
            v-for="goods in type.list.slice(0, 6)"
            :key="goods.gid"
            @click="toGoodsDetails(goods)"
          >
            <!-- {{ goods.title }} -->
            <img v-lazy="goods.url" />
            <transition name="fade" mode="out-in">
              <div class="goods-desc">{{ goods.index }}# {{ goods.title }}</div>
            </transition>
          </div>
        </div>
        <div class="main">
          <div
            class="goods-operate"
            v-for="(item, itemIndex) in type.list"
            :key="itemIndex"
          >
            <div>{{ itemIndex }}. {{ item.title }}</div>
            <div>
              <el-button @click="del(type.list, itemIndex)">删除</el-button>
              <el-button
                v-if="itemIndex != 0"
                @click="moveUp(type.list, itemIndex)"
              >
                上移
              </el-button>
              <el-button
                v-if="itemIndex != type.list.length - 1"
                @click="moveDown(type.list, itemIndex)"
              >
                下移
              </el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'HomeManage',
  data () {
    return {
      typeList: [
        {
          id: '1',
          name: 'T恤',
          list: [
            {
              id: '7030',
              index: '7030',
              title: '彪马情侣短T',
              url: require('@/assets/images/200.jpg')
            },
            {
              id: '7031',
              gid: '7031',
              title: '彪马情侣短T(1)',
              url: require('@/assets/images/200(1).jpg')
            }
          ]
        }
      ]
    }
  },
  methods: {
    addType (list) {
      list.push({
        id: list.length,
        name: list.length,
        list: []
      })
    },
    delType (list, i) {
      list.splice(i, 1)
    },
    add (list) {
      list.push({
        id: 'item' + list.length,
        name: 'item' + list.length,
        gid: 'item' + list.length,
        index: 'item' + list.length,
        title: '彪马情侣短T',
        url: require('@/assets/images/200.jpg')
      })
    },
    del (list, i) {
      list.splice(i, 1)
    },
    moveUp (list, i) {
      list.splice(i - 1, 0, list.splice(i, 1)[0])
    },
    moveDown (list, i) {
      list.splice(i + 1, 0, list.splice(i, 1)[0])
    },
    save () {
      console.log('保存', this.typeList)
    }
  }
}
</script>
<style lang="scss" scoped>
.homeManage {
  min-height: calc(100vh - 125px);
  padding: 20px;
  background-color: $color_bg_gray;
  .menu {
    height: 40px;
    font-size: $font_size_title;
    color: $color_title;
    border-bottom: 3px solid $color_primary;
    font-weight: bold;
    background-color: $color_bg_white;
    .content {
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      div {
        margin-right: 12px;
        cursor: pointer;
        &:hover {
          color: $color_primary;
        }
      }
    }
  }
  .operate-btns {
    margin-top: 10px;
  }
  .type-list {
    .type {
      cursor: pointer;
      .type-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        border: 1px solid #ddd;
        border-bottom: 1px solid $color_primary;
        background: $color_bg_white;
        margin-top: $padding;
        .type-title {
          font-size: $font_size_title;
          text-align: left;
          line-height: 40px;
          font-weight: bold;
        }
      }
      .main{
        .goods-operate{
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 10px;
        }
      }
      .goods-list {
        background-color: $color_bg_white;
        padding: 0 1px 0 0;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        .goods {
          border-top: 1px solid $color_bg_white;
          border-left: 1px solid $color_bg_white;
          width: calc(33.333% - 1px);
          height: auto;
          overflow: hidden;
          background: #fff;
          font-size: 0;
          position: relative;
          img {
            width: 100%;
            transform: scale(1);
            transition: transform 0.5s;
          }
          .goods-desc {
            font-size: $font_size_normal;
            position: absolute;
            bottom: 0;
            line-height: 40px;
            color: #ffffff;
            height: 60px;
            text-align: center;
            border-top: 1px solid $color_primary;
            width: 100%;
            background-color: #0000007c;
            // display: none;
            transform: translateX(-100%);
            transition: transform 0.5s;
          }
          &:hover {
            .goods-desc {
              // display: block;
              transform: translateX(0);
              transition: transform 0.5s;
            }
            img {
              transform: scale(1.1);
              transition: transform 0.5s;
            }
          }
        }
      }
    }
  }
}
</style>
